import Image from "next/image";
import AddComments from "./components/Addcomments/page";
import CommentCard from "./components/Allcomments/page";
import CommentsHeader from "./components/Commentheader/page";

export default function Home() {
  return (
    <div>
      <div className="min-h-screen bg-background">
         <div className="container mx-auto px-4 py-8 max-w-4xl">
       <header className="text-center mb-12"><h1 className="text-4xl font-bold text-foreground mb-4">Comment System</h1><p className="text-xl text-muted-foreground max-w-2xl mx-auto">Share your thoughts and engage with the community. Join the conversation below!</p></header>
      
      
        <div className="space-y-8">
     <AddComments/>
 <div className="space-y-6">
  <CommentsHeader
  totalComments={6}
  onSortChange={(sort) => console.log("Sorting:", sort)}
/>

     <div className="space-y-4">
      <CommentCard
  initials="DR"
  name="David Rodriguez"
  email="david.rodriguez@agency.net"
  company="Creative Agency"
  comment="Hey, this is my comment!"
/>

     </div>
     </div>
    </div>
        </div>


       
      </div>
    </div>
  );
}
